* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  scroll-behavior: smooth;
}
.main {
  padding: 28px 7%;
  background: transparent linear-gradient(180deg, #ffffff 0%, #eaebef 100%) 0%
    0% no-repeat padding-box;
  box-shadow: 0px 2px 8px #00000014;
  height: auto;
}
.primary {
  width: 150px;
  height: 40px;
  border-radius: 25px;
  background-color: #1cb3bc;
  color: #fff;
  box-shadow: 0px 3px 8px #1cb3bc66;
  border: none;
}
.header .options {
  display: inline-block;
  margin: 10px 30px;
  vertical-align: top;
}
.header .options a {
  margin: 20px;
  text-decoration: none;
  color: #18478b;
  font-size: 16px;
  text-transform: uppercase;
}
.header .options2 {
  margin: 0 10px;
  float: right;
}
.header .options2 a {
  margin: 20px;
  text-decoration: none;
  color: #18478b;
  font-size: 16px;
}
.social,.info,.achievements {
  display: inline-block;
}
.vr {
  width: 1px;
  border-left: 2px solid #eaebef;
  margin: 15px 20px;
}
.social img {
  margin: 10px 5px;
}
.social p {
  letter-spacing: 1.4px;
  color: #4f5967;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  height: 100%;
  margin-left: 11px;
  font-size: 14px;
}
.info{
    vertical-align: middle;
    width: 200px;
    margin-top: -130px;
    margin-left: -70px;
}
.info h3{
    font-size: 24px;
    color: #1CB3BC;
    font-weight: bold !important;
}
.info h3 span{
    font-size: 16px;
    color: #4F5967;
    font-weight: 400 !important;
}
.info .id{
    font-size: 14px;
    font-weight: bold !important;
    color: #707070;
}
.tagline{
    padding: 40px 0 ;
    display: grid;
    width: 400px;
    z-index: 9;
}
.tagline h1{
    font-size: 33px;
    color: #0C5CAA;
    text-shadow: 2px 2px 8px #00000029;
    z-index: 12;
}
.info h4{
    font-size: 16px;
    font-weight: bold !important;
    color: #4F5967;
}
.info p{
    color: #707070;
    font-size: 400;
    font-size: 14px;
    margin-bottom: 10px;
    width: 200px;
}
.person{
    display: inline-block;
    background-color: #fff;
    height: 530px;
    width: 440px;
    vertical-align: top;
    margin-top: 40px;
    padding: 10px;
    position: relative;
}
.person img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.person .play{
    width: 50px;
    height: 50px;
    position: absolute;
    left: 40%;
    top: 55%;
}
.person .badgetag{
    position: absolute;
    padding: 10px;
    background-color: #fff;
    border-radius: 7px;
    bottom: 30px;
    right: 5px;
}
.person .badgetag img{
    width: 45px;
    height: 50px;
}
.person .badgetag div{
    display: inline-block;
    vertical-align: top;
}
.person .badgetag h5{
    color: #0C5CAA;
    font-weight: bold !important;
    font-size: 18px;
}
.person .badgetag p{
    color: #0C5CAA;
    font-weight: 500 !important;
    font-size: 16px;
}
.achievements{
    vertical-align: bottom;
    padding: 20px;
}
.achievements h2{
    color: #0C5CAA;
    font-size: 20px;
}
.achievements p{
    width: 350px;
